.profile>:nth-child(1){
background: blue;
height: 6rem;
}
.profile>:nth-child(2){
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    top: -2rem;
}
.profile>:nth-child(2)>img{
    border: 3px solid white;
    border-radius: 4rem;
}
.profile>:last-child{
    display: flex;
    justify-content: center;
    align-items: center;
}
p{
    margin: 0;
}
.profile>:last-child>div{
    padding: 0 3rem;
}
.profile>:last-child>div:nth-child(1){
    border-right: 1px solid var(--gray);
}