.overview{
    padding: 5px 1rem;
    margin-top: 1rem;
    background-color: var(--glass);
    border-radius: 1rem;
}
.overview nav li{
    list-style: none;
}
.overview nav{
    display: flex;
    justify-content: space-between;
}
.headname{
    width: 18rem;
    margin-top: 1rem;
}
.headname h2{
    margin: 0;
}
.headname>div{
    display: flex;
}
small {
    color:var(--gray);
}

.pero{
    position: relative;
    left: 12px;
    top: 8px;
   
    background-color: rgba(47, 161, 47, 0.114);
    height: fit-content;
    padding: 2px 10px;
    border-radius: 1rem;
    color: green;
}
.nega{
    color: red;
    background-color: rgba(255, 47, 0, 0.201);
}
.table{
    display: flex;
    margin-top: 1rem;
}

.row{
    width: 40%;
    display: flex;
    flex-direction: column;
}
.table>:nth-child(1){
    border-right: 1px solid var(--gray);
}
.row>div{
    border-bottom: 1px solid var(--gray);
    padding:8px  10px;
}
.row p{
    margin: 0;
}


@media only screen and (max-width: 1200px) {
    .graph {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .headname{
        width: 100%;
        justify-content: center;
        align-items: center;
    }
}
.headgraph{
    display: grid;
    grid-template-columns: 15rem auto;
}

@media only screen and (max-width: 750px) {
    .headgraph{
        width: 100%;
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .headgraph>:nth-child(2){
        width: 100%;
    }
    .stroke{
        width: 10px;
    }
}
#chart {
    max-width: 760px;
    margin: 35px auto;
    opacity: 0.9;
    color:red;
  }
  