.userdata{
    display: grid;
    grid-template-columns:  repeat(3, minmax(0, 1fr));
    gap: 1rem;
}
.revenue{
    background-color: var(--glass);
    padding: 8px;
    border-radius: 1rem;
}
.uil{
    font-size: 1.5rem;
}
.icon{
    display: flex;
    justify-content: space-between;
}
.icon i{
    padding: 7px;
    background-color: blue;
    border-radius: 1rem;
    color: var(--glass);
}
.icon h3{
    margin: 0;
}
.icon .per{
    position: relative;
    top: 1.5rem;
    background-color: rgba(47, 161, 47, 0.114);
    height: fit-content;
    padding: 2px 10px;
    border-radius: 1rem;
    color: green;
}
.icon .downper{
    color: red;
    background-color: rgba(255, 47, 0, 0.201);
}
.icon>div{
    position: relative;
    left: -3rem;
}

@media only screen and (max-width: 950px) {
    .userdata {
        grid-template-columns:  repeat(3, minmax(0, 0.5fr));
    }
    .icon>div{
        position:static;
    }
  }
@media only screen and (max-width: 650px) {
    .userdata {
        display: flex;
        flex-direction: column;
    }
    .icon>div{
        position:static;
    }
  }