.mainpart{
    display: grid;
    grid-template-columns: auto 22rem;
    margin: 1rem;
}
.rightside{
    margin: 1rem;
    margin-top: 0;
    background-color:var(--glass);
    border-radius: 1rem;
}
@media only screen and (max-width: 1200px) {
    .mainpart {
        display: flex;
        flex-direction: column;
    }
    .rightside{
        position: absolute;
        right: 1rem;
        z-index: 1; 
        background-color: rgb(204, 247, 227);
        border-radius: 1rem;
        transition: width 2s linear 1s;    }
    .setrightside{
        transition: width 2s linear 1s;        display: none;
    }
  }