.earning{
    border-top: 1px solid var(--gray);
margin:  1rem;
}
.earning>:nth-child(1)>li{
list-style: none;
}
.earning>:nth-child(1){
    margin-top: 1rem;
    display: flex;
    justify-content: space-between;
}
.bar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
position: relative;
bottom: 2rem;
gap: 5px;
}
h4{
    margin: 0;
}


.per{
    background-color: rgba(47, 161, 47, 0.114);
    height: fit-content;
    padding: 2px 10px;
    border-radius: 1rem;
    color: green;
    margin-right: 0.5rem;
}
.nega{
    color: red;
    background-color: rgba(255, 47, 0, 0.201);
}
