.navbar{
    display: flex;
}
.navbar ol{
    margin-left: auto;
    display: flex;
    gap: 2rem;
    padding-right: 1rem;
}
.navbar ol li{
    list-style: none;
    padding: 0.4rem 0.4rem;
    cursor: pointer;
}
 .search{
    border: none;
    width: 100%;
padding: 0.5rem 1rem;
}
.navbar>ol>:nth-child(1)>i{
    padding:0 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
input{
    background: rgba(0, 0, 0, 0);
    position: relative;
    right: -1rem;
    cursor: pointer;
}
.ico{
    display: none;
}
.bi-x{
    display: none;
  }
.bi-border-width{
    display: none;
}
@media only screen and (max-width: 790px) {
    .list{
        position: absolute;
        top: -40rem;
        flex-wrap: wrap;
        flex-direction: column;
    }
  .setlist{
    position: absolute;
    flex-direction: column;
    background: #005780;
    right: 2rem;
    font-size: 22px;
    color: white;
    z-index: 3;
    width: 9rem;
    border-radius: 1rem;
  }
  .bi-x{
    display: block;
    position: relative;
    left: -2rem;
  }
    .navbar{
        display: flex;
        justify-content: space-between;
    }
    .navbar .bi-border-width{
        margin-right: 2rem;

    }
    .bi-border-width{
        display: block;
    }
}