.card{
    background-color:var(--glass);
    padding: 0.5rem 0.5rem;
    border-radius: 1rem;
}
.header{
    display: flex;
    justify-content: space-between;
}
.card h2{
    margin: 0;
}
.card>:nth-child(2){
    margin-top: 1rem;
}

.product p{
margin: 0;
}
.product{
    display: flex;
    margin-top: 2rem;
    justify-content: space-between;
}
.product>:nth-child(1){
    display: flex;
}
.product>:nth-child(1)>div:nth-child(1){
    background-color: rgb(64, 64, 236);
    padding: 0.3rem;
    color: white;
    border-radius: 1rem;
    margin-right: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.product>:last-child>p{
    background: rgba(0, 0, 0, 0.090);
    padding:4px 1.2rem;
    position: relative;
    top: 0.5rem;
    border-radius: 0.5rem;
}