:root {
  --yellow: linear-gradient(180deg, #F8D49A -146.42%, #FAD79D -46.42%);
  --orange: #fca61f;
  --black: #242d49;
  --gray: #788097;
  --purple: linear-gradient(180deg, #BB67FF 0%, #C484F3 100%);
  --pink: #FF919D;
  --glass: rgba(255, 255, 255, 1);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
  --activeItem: #f799a354;
}
.App{
  background-color:black;
  /* height: 100vh; */
  display: grid;
  grid-template-columns: 16rem auto;
}
.appglass{
  border-top-left-radius: 2rem;
  background: linear-gradient(106.37deg,#ffe1bc 30%,#ffcfd1 50%,#f3c6f1 90%);
  /* background-color: var(--glass); */
  overflow: hidden;
  z-index: 2;
}
.setsidegrid{
  display: none;
}
@media only screen and (max-width: 1500px) {
.App{
display: block;
}
.sideopen{
  position: absolute;
  left: 1px;
  height: 100vh;
  z-index: 1;
}
.sideclose{
  display: none;
}

.sidegrid{
  position: absolute;
  left: 1rem;
 top: 1rem;
 font-size: 2rem;
}

}
