.recent{
    display: flex;
    gap: 1rem;
    font-size: 14px;
}
.recent>:nth-child(1){
    padding: 0.4rem 0.8rem;
    margin: 0.5rem;
    border-radius: 1rem;
    background: rgba(0, 0, 0, 0.103);
}
.recent>:nth-child(2){
    display: flex;
    justify-content: center;
    align-items: center;
}